import { Component, OnInit } from '@angular/core';
import { CommserviceService } from './services/commservice.service';
import 'rxjs/add/operator/map';
import {Router} from '@angular/router';
import { MapcomponentComponent } from './components/mapcomponent/mapcomponent.component';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent implements OnInit  {
  title = 'Discover Edinburgh!';
  lat: number = 51.678418;
  lng: number = 7.809007;
  //at: number = 35;
  //ng: number = 35;

  budget: number;
  isDev:boolean;
  //schedules: any =  [{lat:23,lng:36},{lat:43,lng:76},{lat:13,lng:56}];
  schedules: object ;

  constructor(
    private router:Router,
    private commserviceService: CommserviceService,
  ) { }

  ngOnInit() {
    //this.router.navigate(['map']);
  }

  onSubmit(){
    console.log('testing');
    const userpref = {
      budget: this.budget
  }

    this.commserviceService.getBudget(userpref).subscribe(data => {
      // if(data.success){
      //     console.log(data);
      //
      // } else {
      //   console.log(data);
      // }
      this.schedules = data;
      console.log(this.budget);
      //console.log(data);
      //---------------------
      var arr = [];
      data.forEach(function(current_value) {
        var keys = Object.keys(current_value);
        for(var i=0;i<keys.length;i++){
          var key = keys[i];
          if (key == 'schedules'){
            console.log(current_value[key][0].place.lat);
            arr.push({
              lat: current_value[key][0].place.lat,
              lng: current_value[key][0].place.lng
            });
          }
        }
      });

      // data.forEach(function(current_value) {
      //   var keys = Object.keys(current_value);
      //   for(var i=0;i<keys.length;i++){
      //     var key = keys[i];
      //     if (key == 'schedules'){
      //       console.log(current_value[key][0]);
      //       this.schedules.push({
      //         lat: current_value[key][0].place.lat,
      //         lng: current_value[key][0].place.lng
      //       });
      //     }
      //   }
      // });
      console.log('object created');
      //console.log(arr);
      this.schedules = arr;
      console.log(this.schedules);
      //this.ngOnInit();
      //this.router.navigate(['map']);
    });
  }


}



