import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';
import { CommserviceService } from './services/commservice.service'
import { RouterModule, Routes } from '@angular/router'

import { AppComponent } from './app.component';
import { AgmCoreModule } from '@agm/core';
import { MapcomponentComponent } from './components/mapcomponent/mapcomponent.component';

const appRoutes: Routes =  [
  {path:'map', component: MapcomponentComponent}
]

@NgModule({
  declarations: [
    AppComponent,
    MapcomponentComponent
  ],
  imports: [
    BrowserModule,
    FormsModule,
    RouterModule.forRoot(appRoutes),
    HttpModule,
    AgmCoreModule.forRoot({
    apiKey: 'AIzaSyAqnwMbodqpzWtOL6Z_WOGkzn45uMfwOII'
  })
  ],
  providers: [CommserviceService],
  bootstrap: [AppComponent]
})
export class AppModule { }



